*,
*:after,
*:before {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-decoration: none;
}

header {
  background-color: #f00;
  padding: 10px;
}
header ul {
  background-color: #fff;
}
header ul li {
  background-color: aquamarine;
  margin-bottom: 5px;
}

main {
  background-color: purple;
}

footer {
  background-color: limegreen;
}